.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
  opacity: 1;
  z-index: 9000;
  transition: opacity $animation-time $animation-function;
  @media screen and (max-width: 768px) {
    padding: 94px 15px;
    overflow-y: scroll;
  }
  @media screen and (max-width: 1199px) {
  }
}
.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}
